.body-container {
    margin-top: 167px;
    width: 100%;
    display: block;
    min-height: 1000px;
    section {
        margin-top: 100px;
        .center {
            margin: 0 auto;
        }
        .font {
            font-family: Avenir;
            font-style: normal;
            font-size: 1.125rem;
            font-weight: normal;
            color: #000000;
        }
        .home-title {
            text-align: center;
            font-family: Comfortaa;
            font-style: normal;
            font-weight: bold;
            font-size: 2.25rem;
            line-height: 2rem;
        }

        .section-wrapper {
            padding-top: 5%;
            display: flex;
            width: 80%;
            min-width: 1100px;
            height: 100%;
            margin: 0 auto;
        }
    }
    #section2 {
        height: auto;
        .section-wrapper {
            height: 100%;
            flex-direction: column;
            display: flex;
            .lower-wrapper {
                margin-top: 3.75rem;
                display: flex;
                justify-content: space-between;
                .left-wrapper {
                    width: 45%;
                    min-width: 500px;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    .intro {
                        color: #323130;
                        line-height: 1.5rem;
                        text-align: justify;
                    }
                    .feature {
                        height: 128px;
                        background: #f5f5ff;
                        margin-top: 1rem;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        padding: 0 30px;
                        img {
                            height: 70px;
                            width: 70px;
                        }
                        .text-wrapper {
                            width: 80%;
                            height: 100%;
                            display: flex;
                            flex-direction: column;
                            justify-content: space-around;
                            padding: 20px 0;
                            .feature-title {
                                font-weight: 800;
                                font-size: 1.5rem;
                                line-height: 1.5rem;
                            }
                            .feature-info {
                                color: #555555;
                                line-height: 1.5rem;
                                text-overflow: ellipsis;
                                display: -webkit-box;
                                -webkit-box-orient: vertical;
                                -webkit-line-clamp: 2;
                                overflow: hidden;
                            }
                        }
                    }
                }
                .right-wrapper {
                    width: 45%;
                    overflow: hidden;
                }
            }
        }
    }
    #section3 {
        height: auto;
        .section-wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;
            .program-title {
                margin-bottom: 3.75rem;
            }
            ul {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                justify-content: space-between;
                li {
                    display: block;
                    width: 45%;
                    min-width: 460px;
                    list-style: none;
                    height: 240px;
                    margin-bottom: 40px;
                }
            }
        }
    }
    #section4 {
        position: relative;
        width: 100%;
        height: 1800px;
        background: #fff3f4;
        .section4-wrapper {
            justify-content: space-between;
            height: 100%;
            .timeLine-wrapper {
                position: relative;
                width: 25%;
                min-width: 250px;
                .timeLine-item-wrapper {
                    margin-top: 50px;
                    .event-item {
                        list-style: none;
                        display: flex;
                        justify-content: center;
                    }
                }
            }
            .schoolInfo-wrapper {
                position: relative;
                width: 40%;
                display: flex;
                flex-direction: column;
                align-items: center;
                .home-title {
                    min-width: 200px;
                }
                .infoCard-wrapper {
                    margin-top: 50px;
                    .infoCard-item {
                        list-style: none;
                        width: 535px;
                        min-width: 300px;
                        margin-bottom: 2rem;
                    }
                }
                .btn {
                    outline: 0;
                    border: 0 none;
                    height: 44px;
                    width: 160px;
                    background: #ff656a;
                    border-radius: 100px;
                    line-height: 100%;
                    display: flex;
                    text-align: center;
                    color: #ffffff;
                    font-weight: 800;
                    justify-content: center;
                    &:hover {
                        box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
                            0 17px 50px 0 rgba(0, 0, 0, 0.19);
                    }
                    &:focus {
                        border: 0 none;
                        outline: none;
                    }
                    &:active {
                        box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.24),
                            0 17px 50px 0 rgba(0, 0, 0, 0.19);
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1100px) {
    html {
        font-size: 14px;
    }
    section {
        .section-wrapper {
            min-width: 0 !important;
        }
    }
    #section2 {
        .lower-wrapper {
            justify-content: center !important;
            .left-wrapper {
                min-width: 460px !important;
            }
            .right-wrapper {
                display: none;
            }
        }
    }
    #section3 {
        ul {
            flex-direction: column !important;
            align-items: center;
        }
    }

    #section4 {
        height: 3000px !important;
        .section4-wrapper {
            flex-direction: column;
            align-items: center;
            justify-content: flex-start !important;
            .schoolInfo-wrapper {
                margin-top: 100px;
                .infoCard-item {
                    width: 460px !important;
                    height: 260px;
                }
            }
        }
    }
}
