.mapcard-container {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	.mapcard-item {
		background: #ffffff;
		border-radius: 4px;
		overflow: hidden;
		display: flex;
		flex-direction: row;
		width: 100%;
		height: 100%;
		// width: 555px;
		// height: 302px;
		.map-wrapper {
			flex: 0.8;
			img {
				width: 100%;
				height: 100%;
			}
		}
		.mapcard-info-wrapper {
			font-family: Avenir;
			font-style: normal;
			font-weight: normal;
			font-size: 1.125rem;
			line-height: 1.5rem;
			color: #555555;
			flex: 1;
			padding-left: 3rem;
			.mapcard-title {
				margin-top: 2.5rem;
				margin-bottom: 1.5rem;
				height: 24px;
				font-weight: 500;
				font-size: 1.5rem;
				line-height: 1.5rem;
				color: #000000;
			}
			.mapcard-address {
				margin-bottom: 1.5rem;
				display: flex;
				align-items: flex-start;
				.mapcard-address-text {
					display: inline-block;
					margin-left: 8px;
				}
			}
			.mapcard-hours {
				margin-bottom: 1.5rem;
				display: flex;
				align-items: flex-start;
				.mapcard-hours-text {
					display: inline-block;
					margin-left: 8px;
				}
			}
			.mapcard-cellphone {
				display: flex;
				align-items: flex-start;
				.mapcard-cellphone-text {
					display: inline-block;
					margin-left: 8px;
				}
			}
		}
	}
}
