.card-container {
	width: 100%;
	height: 100%;
	padding: 40px;
	background: #fffbf1;
	border-radius: 4px;
	.card-wrapper {
		display: flex;
		flex-direction: column;
		font-family: Avenir;
		font-style: normal;
		font-weight: normal;
		font-size: 1.125rem;
		color: #000000;
		height: 100%;
		justify-content: space-between;
		.index {
			height: 22px;
			width: 18px;
			line-height: 18px;
			font-weight: 800;
			border-bottom: 2px solid #000000;
		}
		.title {
			font-weight: 500;
			font-size: 1.5rem;
		}
	}
}
