.timeLine-container {
	position: relative;
	display: flex;
	width: 100%;
	min-width: 10rem;
	height: auto;
	justify-content: center;
	font-family: Avenir;
	font-style: normal;
	font-weight: normal;
	font-size: 1rem;
	line-height: 1rem;
	mix-blend-mode: normal;
	display: flex;
	.timeLine-icon {
		flex: 1;
		display: flex;
		flex-direction: column;
	}
	.timeLine-text {
		flex: 6;
		margin-left: 5%;
		.event-date {
			margin-top: 4%;
			font-weight: 800;
			color: #000000;
		}
		.event-info {
			margin-top: 4%;
			color: #555555;
			line-height: 1.4rem;
		}
	}
}
